import React from 'react';
import Api from './Api';

export default class LoginPanel extends React.Component {
  
  constructor(props){
    super(props);
    this.state = {
      error: '',
      username: '',
      password: '',
    };
  }

  async doLogin(){
    try{
      const { success, message } = await Api.POST('/login.php', {
        username: this.state.username.trim(),
        password: this.state.password.trim(), 
      });
      if(!success) throw message;
      if(typeof this.props.onLoggedIn === 'function') this.props.onLoggedIn();
    }catch(error){
      this.setState({ error: error.toString() });
      setTimeout(()=>this.setState({ error: '' }), 1500);
    }
  }

  render(){
    return (
      <form id="LoginForm" onSubmit={ e=>e.preventDefault()||this.doLogin() }>
        { this.state.error && <span className="alert alert-danger">{this.state.error}</span> }
        <input
          type="text"
          className="form-control"
          placeholder="Username"
          value={this.state.username}
          onChange={ e=>this.setState({ username: e.target.value }) }
        />
        <input
          type="password"
          className="form-control"
          placeholder="Password"
          value={this.state.password}
          onChange={ e=>this.setState({ password: e.target.value }) }
        />
        <input
          type="submit"
          className="btn btn-primary"
          value="Login"
        />
      </form>
    )
  }

}