import React from 'react';
import './AdminPanel.css';

import Users from './admins/Users';
import Household from './admins/Household';

export default class AdminPanel extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      loading: true,
      pages: [Users, Household],
    };
  }

  async componentDidMount(){
    this.setState({ page: this.state.pages.length > 0 ? this.state.pages[0] : null });
    this.setState({ loading: false });
  }

  render(){
    if(this.state.loading)
      return (
        <span>Loading, please wait...</span>
      )
    const PageComponent = this.state.page;
    return (
      <div className="wrapper">
        <div
          className="navbar navbar-dark bg-dark text-light p-3"
        >
          <span className="navbar-brand">Admin Panel</span>
          <div className="dropdown">
            <i 
              className="fas fa-bars"
              type="button" 
              id="AdminMenu"
              data-bs-toggle="dropdown" 
              aria-expanded="false"
            >
            </i>
            <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="AdminMenu">
              {this.state.page && this.state.pages.map((page, key)=>(
                <li key={key}>
                  <span 
                    className={`dropdown-item${page===this.state.page?' active':''}`}
                    onClick={ ()=>this.setState({ page }) }
                  >
                    {page.prototype.constructor.name}
                  </span>
                </li>
              ))}
              <li>
                <span
                  className="dropdown-item text-danger"
                  onClick={ ()=>this.props.onLogout&&this.props.onLogout() }
                >
                  Logout
                </span>
              </li>
            </ul>
          </div>
        </div>
        <PageComponent id="content" className="container-fluid"/>
      </div>
    );
  }
  
}