import React from 'react';
import './App.css';

import Api from './Api';
import LoginPanel from './LoginPanel';
import AdminPanel from './AdminPanel';
import UserPanel from './UserPanel';

export default class App extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      loading: true,
      isLoggedIn: false,
      user: null,
      error: '',
      buttonDisabled: false,
    };
  }

  async CheckLoginStatus(){
    try{
      this.onLoggedIn();
    }catch(error){
      this.setState({ isLoggedIn: false, error: error.toString() });
    }
    setTimeout(()=>this.CheckLoginStatus(), 60000); // Every 1 minute
  }

  async componentDidMount(){
    await this.CheckLoginStatus();
    this.setState({ loading: false });
  }

  async onLoggedIn(){
    try{
      const { success, message, data } = await Api.GET('/session.php');
      if(!success) throw message;
      this.setState({ isLoggedIn: true, user: data });
    }catch(error){
      this.setState({ error: error.toString() });
    }
  }

  async doLogout(){
    try{
      const { success, message } = await Api.POST('/logout.php');
      if(!success) throw message;
      this.setState({ isLoggedIn: false, user: null });
    }catch(error){
      this.setState({ error: error.toString() });
    }
  }

  render(){
    if(this.state.loading)
      return (<span>Loading, please wait...</span>);
    if(!this.state.isLoggedIn)
      return (
        <LoginPanel 
          onLoggedIn={ ()=>this.onLoggedIn() }
        />
      );
    if(this.state.user.login_is_admin === '1')
      return (
        <AdminPanel 
          onLogout={ ()=>this.doLogout() }
        />
      )
    return (
      <UserPanel
        onLogout={ ()=>this.doLogout() }
      />
    )
  }

}
