import React from 'react';
import Api from '../Api';

import AddUserModal from './modals/AddUserModal';
import EditUserModal from './modals/EditUserModal';

export default class Users extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      page: 1,
      loading: true,
      users: null,
      totalPage: 1,
      error: '',
      buttonDisabled: false,
    };
  }

  alert(error){
    this.setState({ error: error?.toString() });
    setTimeout(()=>this.setState({ error: '' }), 1500);
  }

  async componentDidMount(){
    this.setState({ loading: true });
    await this.getUsers(this.state.page);
    this.setState({ loading: false });
  }

  async getUsers(page=1){
    try{
      const { success, message, data } = await Api.GET(`/users.php?page=${page}`);
      if(!success) throw message;
      const { users, totalPage } = data;
      if(users.length === 0 && page > totalPage){
        this.setState({ page: totalPage });
        return this.getUsers(totalPage);
      }
      this.setState({ users, totalPage });
    }catch(error){
      this.alert(error);
    }
  }

  async AddUser(user){
    this.setState({ buttonDisabled: true });
    try{
      const { success, message } = await Api.PUT('/user.php', user);
      if(!success) throw message;
      await this.getUsers(this.state.page);
    }catch(error){
      this.alert(error);
    }
    this.setState({ buttonDisabled: false });
  }

  async EditUser(user){
    this.setState({ buttonDisabled: true });
    try{
      const { success, message } = await Api.PATCH('/user.php', user);
      if(!success) throw message;
      await this.getUsers(this.state.page);
    }catch(error){
      this.alert(error);
    }
    this.setState({ buttonDisabled: false });
  }

  async DeleteUser({ id }){
    this.setState({ buttonDisabled: true });
    try{
      const { success, message } = await Api.DELETE('/user.php', { id });
      if(!success) throw message;
      await this.getUsers(this.state.page);
    }catch(error){
      this.alert(error);
    }
    this.setState({ buttonDisabled: false });
  }

  async NavigateTo(page_){
    this.setState({ loading: true });
    const page = Math.max(1, Math.min(this.state.totalPage, page_));
    if(page !== this.state.page){
      this.setState({ page });
      await this.getUsers(page);
    }
    this.setState({ loading: false });
  }

  render(){
    if(this.state.loading)
      return (
        <span>Loading, please wait...</span>
      )
    return (
      <div id={this.props.id} className={this.props.className}>
        {this.state.error && <div className="toast toast-error">{this.state.error}</div>}
        <div className="w-100 p-3">
          <h3 className="float-start">User management</h3>
          <button 
            className="btn btn-secondary float-end"
            type="button" 
            data-bs-toggle="modal" 
            data-bs-target="#AddUserModal"
            disabled={this.state.buttonDisabled}
          >
            New
          </button>
          <AddUserModal 
            id="AddUserModal" 
            className="modal"
            onSubmit={ user=>this.AddUser(user) }
          />
        </div>
        <table className="table">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Name</th>
              <th scope="col">IC Number</th>
              <th scope="col">Phone No.</th>
              <th scope="col">Action</th>
            </tr>
          </thead>
          <tbody>
            {this.state.users && this.state.users.map((user, key)=>(
              <tr key={key}>
                <th scope="row">{key+1}</th>
                <td>{user.name}</td>
                <td>{user.ic}</td>
                <td>{user.phone}</td>
                <td>
                <div className="dropdown">
                  <button 
                    className="btn btn-primary" 
                    type="button" 
                    id={`Manage${key}`} 
                    data-bs-toggle="dropdown" 
                    aria-expanded="false"
                    disabled={this.state.buttonDisabled}
                  >
                    Manage
                  </button>
                  <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                    <li>
                      <span 
                        className="dropdown-item text-secondary"
                        type="button" 
                        data-bs-toggle="modal" 
                        data-bs-target={`#EditUserModal${key}`}
                        disabled={this.state.buttonDisabled}
                      >
                        Edit
                      </span>
                    </li>
                    <li>
                      <span
                        className="dropdown-item text-danger"
                        onClick={ ()=>this.DeleteUser(user) }
                      >
                        Delete
                      </span>
                    </li>
                  </ul>
                </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        {this.state.totalPage === 1 ? null : <nav id="UserPagination" aria-label="User Pagination">
          <ul className="pagination">
            <li 
              className={`page-item${this.state.page===1?' disabled':''}`}
              onClick={ ()=>this.NavigateTo(this.state.page-1) }
            >
              <span className="page-link">
                <i className="fas fa-less-than"/>
              </span>
            </li>
            {Array(this.state.totalPage).fill().map((_,i)=>i+1).map((page, key)=>(
              <li 
                key={key} 
                className={`page-item${page===this.state.page?' active':''}`}
                onClick={ ()=>this.NavigateTo(page) }
              >
                <span className="page-link">{page}</span>
              </li>
            ))}
            <li 
              className={`page-item${this.state.page===this.state.totalPage?' disabled':''}`}
              onClick={ ()=>this.NavigateTo(this.state.page+1) }
            >
              <span className="page-link">
                <i className="fas fa-greater-than"/>
              </span>
            </li>
          </ul>
        </nav>}
        {this.state.users && this.state.users.map((user, key)=>(
          <EditUserModal
            key={key}
            id={`EditUserModal${key}`}
            className="modal"
            name={user.name}
            ic={user.ic}
            phone={user.phone}
            onSubmit={ ({ name, ic, phone })=>this.EditUser({ id: user.id, name, ic, phone }) }
          />
        ))}
      </div>
    );
  }

}