export default class Api {
  static request(method, path, data){
    const options = {
      method,
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      credentials: 'include',
    }
    if(method !== 'GET') options.body = JSON.stringify(data);
    return fetch(`https://system-ijad-ii07.000webhostapp.com${path}`, options)
      .then(res=>res.json())
      .catch(err=>{throw new Error('Server is currently having problems!')});
  }
  static GET(path){ return Api.request('GET', path); }
  static PUT(path, data){ return Api.request('PUT', path, data); }
  static PATCH(path, data){ return Api.request('PATCH', path, data); }
  static POST(path, data){ return Api.request('POST', path, data); }
  static DELETE(path, data){ return Api.request('DELETE', path, data); }
}