import React from 'react';

export default class AddUserModal extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      name: '',
      ic: '',
      phone: '',
      error: '',
    };
  }

  PrettifyIC(ic){
    const match = ic.match(/(\d{6})(\d{2})(\d{4})/);
    if(!match) return ic;
    const [a, b, c] = match.splice(1);
    return `${a}-${b}-${c}`;
  }

  PrettifyPhoneNo(phone){
    const match = phone.match(/(\d{3})(\d{7,8})/);
    if(!match) return phone;
    const [a, b] = match.splice(1);
    return `${a}-${b}`;
  }

  IsNumber(event, prop, maxLen){
    const { key, keyCode } = event, val = this.state[prop];
    if(keyCode === 8 && val.length > 0) 
      this.setState({ [prop]: val.substring(0, val.length-1) });
    if(val.length >= maxLen) return;
    else if(val.length < maxLen && keyCode >= 40 && /\d/.test(key))
      this.setState({ [prop]: `${val}${key}` })
  }

  Submit(){
    let { name, ic, phone } = this.state;
    if(!/\d{12}/.test(ic)) return this.setState({ error: 'Invalid IC number format!' });
    else if(!/\d{10,11}/.test(phone)) return this.setState({ error: 'Invalid phone number format!' });
    else if(typeof this.props.onSubmit === 'function'){
      this.props.onSubmit({
        name,
        ic: this.PrettifyIC(ic),
        phone: this.PrettifyPhoneNo(phone),
      });
      this.setState({
        name: '',
        ic: '',
        phone: '',
      });
    }
  }
  
  render(){
    return (
      <div id={this.props.id} className={this.props.className} tabIndex="-1">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">New User</h5>
            </div>
            <div className="modal-body">
              <form className="container">
                <div className="row form-group m-2">
                  <label className="col-4 text-start" htmlFor="name">Name</label>
                  <input 
                    className="col-8 text-center" 
                    type="text" 
                    name="name"
                    value={this.state.name}
                    onChange={ e=>this.setState({ name: e.target.value.toUpperCase().substring(0, 100) }) }
                  />
                </div>
                <div className="row form-group m-2">
                  <label className="col-4 text-start" htmlFor="ic">IC Number</label>
                  <input 
                    className="col-8 text-center" 
                    type="text" 
                    name="ic"
                    value={`${this.PrettifyIC(this.state.ic)}`}
                    onKeyDown={ e=>this.IsNumber(e, 'ic', 12) }
                    onChange={ ()=>{} }
                  />
                </div>
                <div className="row form-group m-2">
                  <label className="col-4 text-start" htmlFor="phone">Phone No.</label>
                  <input 
                    className="col-8 text-center" 
                    type="text" 
                    name="phone"
                    value={`${this.PrettifyPhoneNo(this.state.phone)}`}
                    onKeyDown={ e=>this.IsNumber(e, 'phone', 11) }
                    onChange={ ()=>{} }
                  />
                </div>
              </form>
            </div>
            <div className="modal-footer">
              <button 
                type="button" 
                className="btn btn-secondary" 
                data-bs-dismiss="modal"
              >
                Cancel
              </button>
              <button 
                type="button" 
                className="btn btn-primary"
                data-bs-dismiss="modal"
                onClick={ ()=>this.Submit() }
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      </div>
    )
  }

}